<template>
    <div class='setting center'>
        <Row type='flex' justify="center" :gutter='16'>
            <Col :xs='22' :sm='10'>
                <div class="setting-list">
                    <div class='setting-item center' v-for='setting in bool_list' :key='setting'>
                    <MyInput  type='bool' v-model="setting.value" >
                        <slot>{{setting.text}}</slot>
                    </MyInput>
                    </div>
                </div>
            </Col>
            <Col :xs='22' :sm='10'>
                <div class="setting-list">
                    <div class='setting-item center' v-for='setting in text_list' :key='setting'>
                    <MyInput type='text' v-model="setting.value" append='分钟'>
                        <slot>{{setting.text}}</slot>
                    </MyInput>
                    </div>
                <div v-show='bool_list.FLEXIBLE.value' class='setting-item center' v-for='setting in select_list' :key='setting'>
                    <MyInput type='select' v-model="setting.value" :options='setting.option_list'>
                        <slot>{{setting.text}}</slot>
                    </MyInput>
                </div>
                </div>
            </Col>
        </Row>
        <div class="center">
            <MyButton classes='setting-button' color='limegreen' @click='change'>确认修改</MyButton>
        </div>
    </div>
</template>

<script>
import MyInput from './MyInput'
import MyButton from './MyButton'
import $ from 'jquery'
export default {
    components: {
        MyInput,
        MyButton
    },
    data: function () {
        window._setting = this
        return {
            bool_list: {
                REMIND_ALIVE: {
                    text: '提醒开关',
                    value: false
                },
                ROBOT_REPLY: {
                    text: '智能回复开关',
                    value: false
                },
                VOICE_REPLY: {
                    text: '语音回复开关',
                    value: false
                },
                FLEXIBLE: {
                    text: '灵活调整开关',
                    value: false
                },
            },
            text_list: {
                REMIND_WAIT: {
                    text: '提醒间隔',
                    value: 2
                },
                REMIND_BEFORE: {
                    text: '提前提醒时间',
                    value: 30
                },
                UPDATE_WAIT: {
                    text: '信息更新间隔',
                    value: 60
                },
            },
            select_list: {
                FLEXIBLE_DAY: {
                    text: '灵活调整日期',
                    option_list: [
                        {
                            value: '星期一',
                            label: '星期一'
                        },
                        {
                            value: '星期二',
                            label: '星期二'
                        },
                        {
                            value: '星期三',
                            label: '星期三'
                        },
                        {
                            value: '星期四',
                            label: '星期四'
                        },
                        {
                            value: '星期五',
                            label: '星期五'
                        },
                        {
                            value: '星期六',
                            label: '星期六'
                        },
                        {
                            value: '星期日',
                            label: '星期日'
                        }
                    ],
                    value: '星期一'
                }
            },
        }
    },
    methods: {
        init: function () {
            $.get('/helper/setting/init/', function (data) {
                for (var each in data.bool_list) {
                    var setting = data.bool_list[each]
                    _setting.bool_list[setting.name].value = setting.val
                }
                for (var each in data.text_list) {
                    var setting = data.text_list[each]
                    _setting.text_list[setting.name].value = setting.val
                }
                for (var each in data.select_list) {
                    var setting = data.select_list[each]
                    _setting.select_list[setting.name].value = setting.val
                }
            })
        },
        change: function () {
            var res = new Object()
            var all_list = [this.bool_list, this.text_list, this.select_list]
            for (var count in all_list) {
                for (var each in all_list[count]) {
                    res[each] = all_list[count][each].value
                }
            }
            $.post('/helper/setting/change/', JSON.stringify(res), function (data) {
                if (data.res) {
                    _setting.$Modal.success({
                        title: '',
                        content: data.msg
                    })
                }
                else {
                    _setting.$Modal.error({
                        title: '',
                        content: data.msg
                    })
                }
            })
            // console.log(JSON.stringify(res))
        }
    },
    mounted: function () {
        this.init()
    }
}


</script>

<style>
.setting {
    width: 100%;
    height: 100%;
    /*flex-direction: column;*/
    /*flex-wrap: nowrap;*/
}
.setting-list{
    margin-top: 1em;
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.setting-item{
    width: 100%;
    margin: 1em;
}
.setting-button{
    margin-top: 1em;
    margin-bottom: 2em;
    text-align: center;
    width: 7em;
}
</style>



