<template>
    <div class='log center'>
        <div class="log-box">
            <p v-for='line in log_list'>{{line}}</p>
        </div>
        <MyButton class='log-button' @click='get_all'>
            查看全部日志
        </MyButton>
    </div>
</template>

<script>
import MyButton from './MyButton'
import WebSocket from '~/js/web-socket.js'
import $ from 'jquery'

export default {
    components: {
        MyButton,
    },
    data: function () {
        window._log = this
        return {
            log_list: []
        }
    },
    methods: {
        get_log: function (start, count) {
            $.get('/helper/log/get/start=' + start + '&count=' + count, function (data) {
                _log.append_log(data.log_list)
            })
        },
        get_all: function () {
            this.log_list = []
            this.get_log(0, -1)
        },
        get_now: function () {
            var temp_func = ((num) => {
                return num < 10 ? '0' + String(num) : String(num)
            })
            var now = new Date()
            var date_obj = {
                year: now.getYear() + 1900,
                mouth: temp_func(now.getMonth() + 1),
                day: temp_func(now.getDate()),
                hour: temp_func(now.getHours()),
                min: temp_func(now.getMinutes()),
                sec: temp_func(now.getSeconds())
            }
            return '[' + date_obj.day + '/' + date_obj.mouth + '/' + date_obj.year + ' ' + date_obj.hour + ':' + date_obj.min + ':' + date_obj.sec + ']'
        },
        add_time: function (log) {
            for (var i=0; i < log.length; i++) {
                log[i] = this.get_now() + ' ' + log[i]
            }
            return log
        },
        append_log: function (new_log, add_time) {
            console.log(add_time)
            if (add_time) {
                new_log = this.add_time(new_log)
            }
            this.log_list = this.log_list.concat(new_log)
        },
        prepend_log: function (new_log, add_time) {
            if (add_time) {
                new_log = this.add_time(new_log)
            }
            this.log_list = new_log.concat(this.log_list)
        },
        ws_func: function () {
            var ws_scheme = window.location.protocol == "https:" ? "wss" : "ws"
            // this.sock = new WebSocket(ws_scheme + '://192.168.0.100/log/')
            this.sock = new WebSocket(ws_scheme + '://' + window.location.host + "/log/");
            this.sock.onmessage = function (message) {
                var data = JSON.parse(message.data)
                _log.prepend_log([data.msg], true)
            }
        },
        init: function () {
            this.get_log(0, 50)
            this.ws_func()
        }
    },
    mounted: function () {
        this.init()
    },
    beforeDestroy: function () {
        if (this.sock) {
            this.sock.close()
        }
    }
}
</script>

<style>
.log {
    width: 100%;
    height: 100%;
}

.log-box {
    height: 90%;
    width: 100%;
    margin: 1em 2em 0 2em;
    padding: 0.5em;

    text-align: left;
    color: #f6f9f6;
    font: 14px;
    word-wrap: break-word;
    word-break: break-all;
    overflow-y: visible;
    overflow-x: hidden;

    background-color: rgba(0, 0, 0, .65);
    border: 5px groove black;

    /*圆角*/
    -moz-border-radius: 10px;
    border-radius: 10px;
}

.log-button {
    width: 10em;
    text-align: center;
}
</style>



